// /src/components/portfolio/ProjectDetail/ProjectDetail.js

import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { NavLink, useParams } from 'react-router-dom';
import api from '../../../services/api';
import { API_BASE_URL } from '../../../services/authentication';

// Styled components for the Project Detail
const ProjectDetailWrapper = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 0;
`;

const ProjectTitle = styled.h2`
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
  @media (max-width: 376px) {
    font-size:1.5rem;
  }
`;

const ProjectDescription = styled.p`
  font-size: 1.4em;
  line-height: 1.6;
  color: #555;
  margin-bottom: 20px;
  @media (max-width: 376px) {
    font-size:1.2rem;
  }
`;

const ProjectImage = styled.img`
  width: 100%;
  height: auto;
  margin-bottom: 20px;
`;

const ProjectLinks = styled.div`
  display: flex;
  flex-wrap: wrap; /* Allow links to wrap to the next line on smaller screens */
  align-items: center;
  gap: 20px;

  @media (max-width: 768px) {
    flex-direction: column; /* Stack links vertically for smaller screens */
  }
`;

const ProjectLink = styled.a`
  text-decoration: none;
  color: #007bff;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  margin-right: 20px;
  transition: color 0.3s ease;

  &:hover {
    color: #0056b3;
  }
`;

const ImageContainer = styled.div`
  margin: 10px;
  padding: 5px;
  width: 90%;
  height: auto;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  zoom: 100%;
  border-radius: 10px;
`;

const Technology = styled.div`
  font-size: 1.3rem;
`;



const ProjectDetail = () => {
  const {id}=useParams()
  const [project,setProject]=useState(null)

  useEffect(()=>{
    const getProject=async()=>{
      try{
        const project=await api.project.getProjectbyId(id)
        setProject(project)
      }
      catch(err){
        console.log(err)
      }
    }
    getProject()
  },[id])

  if (!project) {
    return <div>Loading...</div>; // You can customize the loading state as needed
  }

  
  return (
    <ProjectDetailWrapper>
      <ImageContainer>
            <Image src={`${API_BASE_URL}/images/`+project.image} alt={`Project: ${project.title}`} />
      </ImageContainer>
      <ProjectTitle>{project.title}</ProjectTitle>
      <ProjectDescription>{project.description}</ProjectDescription>
      <Technology>Technologies: {project.technologies}</Technology>
      <ProjectDescription>Role: {project.role}</ProjectDescription>
      <ProjectDescription>Project Duration: {project.timeline}</ProjectDescription>
      <Technology><NavLink to={`${project.projectlink}`}>Project Link</NavLink></Technology>
      <ProjectLinks>
        {/* <ProjectLink href={liveDemoLink} target="_blank" rel="noopener noreferrer">
          <AiOutlineLink style={{ marginRight: '5px' }} />
          Live Demo
        </ProjectLink> */}
      </ProjectLinks>
    </ProjectDetailWrapper>
  );
};

export default ProjectDetail;
