// /src/components/common/Header/Header.js

import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import Logoimg from '../../../assets/portfolioLogo.png';
import ThemedComponent from '../Theme/ThemedComponent';
import { useAdmin } from '../../../context/AdminContext';
import { useAuth } from '../../../context/AuthContext';
import { Menu } from 'lucide-react';

// Styled components for the Header
const HeaderWrapper = styled.header`
  color: #333; /* Dark text color for the header */
`;

const HeaderContainer=styled.div`
background: linear-gradient(to right, #feb479, #ff7e5f);/* Gradient background */
padding: 20px;
display: flex;
justify-content: space-between;
align-items: center;
`;

const NavigationContainer = styled.nav`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;

  a {
    color: #fff;
    text-decoration: none;
    font-size: 1.4rem;
    transition: color 0.3s ease;

    &:hover {
      color: #ff4500;
    }
  }
  @media (max-width:426px) {
    display: none; 
  }
`;

const Logo = styled(Link)`
  text-decoration: none;
  color: #333; /* Dark text color for the header */
  font-size: 1.5em;
  font-weight: bold;
  display: flex;
  width:100px;
  align-items: center;
  margin:20px;
`;

// Styled component for the logo image
const LogoImage = styled.img`
  width: 100%; /* Adjust the width as needed */
  height: auto;
  margin-right: 10px; /* Adjust the margin as needed */
  object-fit: contain;
`;

const NavList = styled.ul`
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  @media (max-width: 767px) {
    width:100%;
    flex-direction: column;
    align-items: center;
    justify-content:space-evenly;
    text-align: center;
  }
`;

const NavItem = styled.li`
  margin-right: 20px;
  @media (max-width: 767px) {
    margin: 10px 0;
  }
`;

const NavLink = styled(Link)`
  text-decoration: none;
  color: #fff;
  font-size: 1.2em;
  &:hover {
    border-bottom: 2px solid #333; /* Dark border color for hover effect */
  }
  @media (max-width: 768px) {
    font-size: 1em;
  }
`;

const MobileMenuIcon = styled(Menu)`
  display: none; // Hide by default on larger screens

  @media (max-width: 426px) {
    display: block; // Display on screens smaller than 768px
    cursor: pointer;
  }
`;

const MobileMenu = styled.div`
  width:100%;
  display: none; // Hide by default

  @media (max-width: 426px) {
    display: flex;
    flex-direction: column;
    background: #333;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }

  a {
    color: #fff;
    text-decoration: none;
    font-size: 1.4rem;
    transition: color 0.3s ease;

    &:hover {
      color: #ff4500;
    }
  }
`;

const Header = () => {
  const {isAdmin}=useAdmin()
  const {isAuthenticated}=useAuth()
  const [showMobileMenu, setShowMobileMenu] = useState(false);

  const toggleMobileMenu = () => {
    setShowMobileMenu(!showMobileMenu);
  };

  return (
    <HeaderWrapper>
    <HeaderContainer>
        <Logo to="/">
            <LogoImage src={Logoimg} alt="Portfolio Logo" />
        </Logo>
      <NavigationContainer>
        <NavList>
          <NavItem>
            <NavLink to="/about">About</NavLink>
          </NavItem>
          <NavItem>
            <NavLink to="/portfolio">Portfolio</NavLink>
          </NavItem>
          <NavItem>
            <NavLink to="/blog">Blog</NavLink>
          </NavItem>
          <NavItem>
            <NavLink to="/contact">Contact</NavLink>
          </NavItem>
          <NavItem>
            {isAdmin && isAuthenticated ?<NavLink to='/admin'>Dashboard</NavLink>:<NavLink to="/login">Login/SignUp</NavLink>}
          </NavItem>
          <ThemedComponent/>
          {/* Add more navigation links as needed */}
        </NavList>
      </NavigationContainer>
      <MobileMenuIcon size={32} onClick={toggleMobileMenu} />
      </HeaderContainer>
        {showMobileMenu && (
        <MobileMenu>
        <NavList>
          <NavItem>
            <NavLink to="/about">About</NavLink>
          </NavItem>
          <NavItem>
            <NavLink to="/portfolio">Portfolio</NavLink>
          </NavItem>
          <NavItem>
            <NavLink to="/blog">Blog</NavLink>
          </NavItem>
          <NavItem>
            <NavLink to="/contact">Contact</NavLink>
          </NavItem>
          <NavItem>
            {isAdmin && isAuthenticated ?<NavLink to='/admin'>Dashboard</NavLink>:<NavLink to="/login">Login/SignUp</NavLink>}
          </NavItem>
          <ThemedComponent/>
          {/* Add more navigation links as needed */}
        </NavList>
          {/* Add more navigation links as needed */}
        </MobileMenu>
      )}
    </HeaderWrapper>
  );
};

export default Header;
