// /src/components/contact/ContactForm/ContactForm.js

import React, { useState } from 'react';
import styled from 'styled-components';
import { useTheme } from '../../../context/ThemeContext';
import { useForm, ValidationError } from '@formspree/react';

// Styled components for the ContactForm
const FormWrapper = styled.form`
  /* Add styles for the contact form */
  background:${({ theme }) => theme[theme.mode].background};
  color:${({ theme }) => theme[theme.mode].text}
`;

const FormGroup = styled.div`
  margin-bottom: 20px;
`;

const Label = styled.label`
  font-size: 1.2em;
  color: ${({ theme }) => theme[theme.mode].text};
  margin-bottom: 10px;
  display: block;
`;

const Text=styled.p`
  font-size:1.2rem;

`;

const Input = styled.input`
  /* Add input styles */
  width: 100%;
  padding: 10px;
  font-size: 1em;
`;

const TextArea = styled.textarea`
  /* Add textarea styles */
  width: 100%;
  padding: 10px;
  font-size: 1em;
  font-family:san;
`;

const Button = styled.button`
  /* Add button styles */
  background-color: skyblue;
  color: ${({ theme }) => theme[theme.mode].text};
  padding: 10px 20px;
  font-size: 1.2em;
  cursor: pointer;
`;

const ContactForm = () => {
  const [state, handleSubmit] = useForm("mqkrzywn");
  const {theme}=useTheme()

  if (state.succeeded) {
      return <Text>Thanks for joining!</Text>;
  }

  return (
    <FormWrapper onSubmit={handleSubmit} theme={theme} >
      <FormGroup>
        <Label theme={theme} htmlFor="name">Name</Label>
        <Input type="text" id="name" name="name" required />
        <ValidationError 
          prefix="Name" 
          field="name"
          errors={state.errors}
        />
      </FormGroup>
      <FormGroup>
        <Label theme={theme} htmlFor="email">Email</Label>
        <Input type="email" id="email" name="email"  required />
        <ValidationError 
          prefix="Email" 
          field="email"
          errors={state.errors}
        />
      </FormGroup>
      <FormGroup>
        <Label theme={theme} htmlFor="message">Message</Label>
        <TextArea id="message" name="message" rows="6" required></TextArea>
        <ValidationError 
          prefix="Message" 
          field="message"
          errors={state.errors}
        />
      </FormGroup>
      <Button theme={theme} type="submit" disabled={state.submitting}>Submit</Button>
    </FormWrapper>
  );
};

export default ContactForm;
